import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { useTranslation } from "react-i18next";
import { Navigation } from "lucide-react";

interface NavigationConfirmDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  onConfirm: () => void;
  destinationName: string;
}

const getNavigationAppName = (): string => {
  const isIOS = /iPad|iPhone|iPod/.test(navigator.userAgent);
  const isAndroid = /Android/.test(navigator.userAgent);
  
  if (isIOS) {
    return "Apple Maps";
  } else if (isAndroid) {
    return "Google Maps";
  } else {
    return "Google Maps";
  }
};

const NavigationConfirmDialog = ({
  open,
  onOpenChange,
  onConfirm,
  destinationName,
}: NavigationConfirmDialogProps) => {
  const { t } = useTranslation();
  const appName = getNavigationAppName();

  return (
    <AlertDialog open={open} onOpenChange={onOpenChange}>
      <AlertDialogContent className="max-w-md">
        <AlertDialogHeader>
          <div className="flex items-center gap-3 mb-2">
            <div className="p-2 rounded-full bg-primary/10">
              <Navigation className="w-5 h-5 text-primary" />
            </div>
            <AlertDialogTitle>{t("navigation.confirmTitle")}</AlertDialogTitle>
          </div>
          <AlertDialogDescription className="text-left">
            {t("navigation.confirmDescription", { appName, destination: destinationName })}
          </AlertDialogDescription>
        </AlertDialogHeader>
        <AlertDialogFooter className="flex-col sm:flex-row gap-2">
          <AlertDialogCancel className="w-full sm:w-auto">
            {t("common.cancel")}
          </AlertDialogCancel>
          <AlertDialogAction onClick={onConfirm} className="w-full sm:w-auto">
            {t("navigation.openApp", { appName })}
          </AlertDialogAction>
        </AlertDialogFooter>
      </AlertDialogContent>
    </AlertDialog>
  );
};

export default NavigationConfirmDialog;
